@import "../../global.scss";

.portfolio{
    background-color: white;
    display: flex;
    flex-direction: column;
    align-items: center;

    h1{
        font-size: 50px;
    }
    ul{
        margin: 10px;
        padding: 0;
        list-style: none;
        display: flex;
        li{
            font-size: 14px;
            margin-right: 50px;
            padding: 7px;
            border-radius: 10px;
            cursor: pointer;

            &.active{
                background-color: $mainColor;
                color: white;
            }
        }
        
    }
    .container{
        width: 70%;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-wrap: wrap;

        .item{
            width: 33vw;
            height: 25vh;
            border-radius: 20px;
            border: 1px solid rgb(230, 230, 230);
            margin: 10px 20px;
            display: flex;
            align-items: center;
            justify-content: center;
            color: white;
            position: relative;
            
            h3{
                display: flex;
                justify-content: center;
                color: black;
                position: absolute;
                font-size: 20px;
            }
            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
                z-index: 1;
            }

            &:hover{
                img{
                    z-index: 0;
                    opacity: 0.5;
                }
            }
        }
    }
}